import React, { Component } from 'react';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';
import '@uppy/drag-drop/dist/style.min.css';
import '@uppy/status-bar/dist/style.min.css';
import '@uppy/webcam/dist/style.min.css';
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
import { Image, Button } from 'react-bootstrap';
import { TEMP_URL, UPPY_AWS_KEY, UPPY_ROBOT, UPPY_BUCKET, UPPY_REGION, UPPY_KEY, UPPY_COMPANION_URL, UPPY_AWS_SECRET_KEY, TEMP_FOLDER } from '../../../../../actions/constants';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import ReactPlayer from 'react-player';

export default class AttachImage extends Component {
    constructor(props) {
        super(props)
        const { allowedFile } = props
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes:allowedFile ? allowedFile : ['image/*'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: UPPY_KEY
                    },
                    steps: {
                        store: {
                            robot: UPPY_ROBOT,
                            bucket_region: UPPY_REGION,
                            bucket: UPPY_BUCKET,
                            key: UPPY_AWS_KEY,
                            secret: UPPY_AWS_SECRET_KEY,
                            path: TEMP_FOLDER + '${file.id}.${file.ext}'
                        }
                    }
                },
                waitForEncoding: true
            })
            // .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
            // .use(Url, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
    }
    state = {
        backgroundModal: false
    }
    componentDidMount() {
        this.uppy.on('transloadit:result', (stepName, result) => {
            this.props.onClickTouploadImage(`${TEMP_URL}${result.id}.${result.ext}`, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.setState({ backgroundModal: false })
                }, 300)
            })
        })
    }
    onBackgroundClose = event => {
        this.uppy.cancelAll()
        this.setState({ backgroundModal: false })
    }
    onBackgroundOpen = event => {
        this.setState({ backgroundModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppy.cancelAll()
                this.setState({ backgroundModal: false })
            })
        })
    }
    render() {
        const {backgroundImage, id, isVideo, showStatus } = this.props
        const { backgroundModal } = this.state
        const commoncontent = window?.APP_CONTENT?.common ?? {}
        return <>
            <div className="d-flex align-items-start justify-content-between flex-column py-1">
                {!backgroundImage ? <Button id={id} variant='secondary' className="w-100" onClick={() => this.setState({ backgroundModal: true })}><FontAwesomeIcon icon={'circle-plus'}/>{isVideo ? commoncontent?.attach_video ?? "" : commoncontent?.attach_image ?? ""}</Button> : null}
                {backgroundImage ?
                    <div className="bg-white w-100 rounded-1 position-relative">
                        {isVideo ? <ReactPlayer height={300} width={300} id="bg_image" className="object-fit-contain" url={backgroundImage} autoPlay={true} muted={true} loop={true}  /> : <Image id="bg_image" className="object-fit-contain" src={backgroundImage} height={300} width={300} alt="" style={{ maxHeight: 120 }} fulid />}
                        <div className="position-absolute bottom-0 start-0 w-100 f-center">
                            <Button variant="icon" size="sm" className="rounded-circle btn-active-primary m-2 box-shadow border-0" onClick={() => this.setState({ backgroundModal: true })}>
                                <FontAwesomeIcon type='solid' icon={'pen'}/>
                            </Button>
                            {
                                !showStatus ?
                                    <Button variant="icon" size="sm" className="rounded-circle btn-active-primary m-2 box-shadow border-0" onClick={() => this.props.onClickTouploadImage('', () => { })}>
                                        <FontAwesomeIcon icon={'xmark'} />
                                    </Button> : null
                            }
                        </div>
                    </div> : null}
            </div>
            {
                backgroundModal ?
                    <DashboardModal
                        animateOpenClose={true}
                        open={backgroundModal}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppy}
                        onRequestClose={this.onBackgroundClose}
                        closeAfterFinish={true}
                        plugins={['Url']}
                    />
                    : null
            }
        </>
    }

}
