import React, { Component } from 'react'
import { connect } from 'react-redux'
import { HOST_URL } from '../../../../../../actions/api'
import { formatTemplateText, TOAST_ERROR } from '../../../../../../utils'
import { tinyRequest } from '../../../../../../actions/tinyUrlAction';
import { Button, Form, InputGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { logRoles } from '@testing-library/react';

class copyUrl extends Component {

    state = { openSteps: false, copied: false, hash: 'yes', accountDetails: {}, postUrl: '' }
    content = window?.APP_CONTENT?.moderation ?? {};

    componentDidMount() {
        this.copyText();
    }

    sendRequest = () => {
        const { onRaiseRequest, setUserName } = this.props
        // setUserName(this.state.accountDetails.username)
        onRaiseRequest(this.state.hash)
    }

    copyText = () => {
        const { ugcData, custom_msg, requestMode, userData } = this.props
        const postLink = ugcData?.data && ugcData?.data[0] ? ugcData?.data[0].link : ''
        tinyRequest(postLink).then((response) => {
            const { data } = response.data
            this.setState({ postUrl: data?.tiny_url })
        }).catch((ex) => {
            this.setState({ postUrl: postLink })
        })

        if (requestMode == 0) navigator.clipboard.writeText(ugcData.bitlyurl)
        else if (userData?.user?.id == 142546) navigator.clipboard.writeText(custom_msg)
        else navigator.clipboard.writeText(formatTemplateText(this.content?.copy_msg, { custom_msg, hash: this.state.hash }))
    }

    raiseReq = () => {
        const { ugcData, custom_msg, requestMode, userData } = this.props

        if (this.state.hash == '' || !this.state.hash) { TOAST_ERROR('Hashtag for UGC Rights is required!'); return; }
        this.sendRequest()
        if (requestMode == 0) navigator.clipboard.writeText(custom_msg + ' Post : ' + this.state.postUrl + ' . Form Link : ' + ugcData.bitlyurl);
        else if (userData?.user?.id == 142546) navigator.clipboard.writeText(custom_msg)
        else navigator.clipboard.writeText(formatTemplateText(this.content?.copy_msg, { custom_msg, hash: this.state.hash }))
        if ((ugcData.data[0].networkId == 18 || ugcData.data[0].networkId == 2) && requestMode == 0) setTimeout(() => window.open(`https://ig.me/m/${ugcData.data[0].username}`, '_blank'), 400)
        else setTimeout(() => window.open(ugcData.data[0].link, '_blank'), 400)
        setTimeout(() => {
            window.location.replace(`${HOST_URL}content/moderation/${localStorage.getItem('wallId')}`)
        }, 400);
    }


    render() {
        const { ugcData, custom_msg, setStep, requestMode, isRequesting, userData } = this.props
        const { openSteps, copied, hash } = this.state;
        return <>
            <div className="p-lg-6 p-4 py-3 rights_modal_aside_wrap">

                <h3 className='mb-3'>{this.content?.ready_title ?? ""}</h3>

                <div className="form-group ugc_right_col">
                    <Form.Group className="mb-3">
                        <Form.Control as={'textarea'} id="r_msg" maxLength="255" cols="30" rows="6" value={requestMode == 0 ? custom_msg : userData?.user?.id == 142546 ? `${custom_msg}` : formatTemplateText(this.content?.copy_msg, { custom_msg, hash }) || ""} disabled />
                    </Form.Group>
                </div>

                {requestMode == 1 ? <>

                    <div className="form-group ugc_right_col">
                        <Form.Group className="mb-3">
                            <Form.Label htmlFor="ugc_rights_hash_value">{this.content?.ready_enter_hashtag ?? ""} <span className="text-danger">*</span> </Form.Label>
                            <Form.Control as={'textarea'} id="ugc_rights_hash_value" maxLength="255" cols="30" rows="6" value={hash} onChange={e => this.setState({ hash: e.target.value })} >
                            </Form.Control>
                        </Form.Group>
                    </div>
                </> : null}
                {requestMode == 0 ? <>
                    <InputGroup className="mb-3">
                        <InputGroup.Text className="cursor-pointer">
                            <FontAwesomeIcon type={'solid'} icon={'link'} />
                        </InputGroup.Text>
                        <Form.Control className="p-2" size='lg' value={ugcData.bitlyurl ? String(ugcData.bitlyurl) : '---'} />
                    </InputGroup>
                    <Button variant='primary' className='w-100 mb-4' onClick={() => this.setState({ copied: true }, () => this.copyText())}>
                        {!copied ? this.content?.copy_url ?? "" : this.content?.btn_url_copied ?? ""}
                    </Button>
                </> : null}


                <h5>{this.content?.ready_steps_title ?? ""}</h5>
                <ul className={`list-unstyled list-points fs-8`}>
                    <li className='mb-1'>{this.content?.step_1 ?? ""}</li>
                    <li className='mb-1'>{this.content?.step_2 ?? ""}</li>
                    <li className='mb-1'>{this.content?.step_3 ?? ""}</li>
                    <li className='mb-1'>{this.content?.step_4 ?? ""}</li>
                    <li className='mb-1'>{this.content?.step_5 ?? ""}</li>
                </ul>

            </div>

            <div className="d-flex justify-content-end p-4 border-top">
                <Button variant="secondary" className="me-2" onClick={() => setStep(2)}>{this.content?.btn_back ?? ""}</Button>
                <Button className={isRequesting ? 'spinner' : ''} onClick={() => this.raiseReq()}>{this.content?.btn_raise_request ?? ""}</Button>
            </div>

        </>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data,
    }
}

export default connect(mapStateToProps)(copyUrl);