import { Component } from "react";  
import { connect } from "react-redux";
import { Button, Form} from "react-bootstrap";
import { formatTemplateText, TOAST_ERROR } from "../../../../../../utils";
import { FontAwesomeIcon } from "../../../../../../Assets/AssetsHelper";
import { COMPANY_NAME } from "../../../../../../actions/api";

class ChooseMethod extends Component {

    state = {
    };

    content = window?.APP_CONTENT?.moderation ?? {};

    componentDidMount() {
        
    }

    onNextStep = () => {
        const { requestMode } = this.props
        if (requestMode != null) {
            this.props.setStep(1)
        }
        else TOAST_ERROR('Please select the mode for Right Request')
    }


    render() {
        const { setRequestMode, requestMode } = this.props

        return (
                <>
                    <div className="p-lg-6 p-4 py-3 rights_modal_aside_wrap rights_no_wizard">
                        <h3 className="mb-1 h5">{this.content?.mode_title ?? ""}</h3>
                        <p className="text-muted">{formatTemplateText(this.content?.mode_desc ?? "", { company: COMPANY_NAME })}</p>

                        <div className="mb-3 d-flex flex-column">
                            <div value={'viaComments'} className={`cursor-pointer mb-4 d-flex border justify-content-between p-3 rounded-1 ${requestMode == 1 ? 'border-success' : ''}`} onClick={() => setRequestMode(1)}>
                                <div className="d-flex me-3 fw-normal">
                                    <FontAwesomeIcon icon={'comment'} iconClass={'fs-2 mt-1'}/>
                                    <span className="text-start ps-2">
                                        <p className="h5 mb-0 fw-bold">{this.content?.mode_comment_title ?? ""}</p>
                                        <span className="fs-9">{this.content?.mode_comment_desc ?? ""}</span>
                                    </span>
                                </div>
                                <Form.Check.Input type={'radio'} name={'rights-via'} className="d-none" />
                                <span className={`w-20px fs-6 ${requestMode == 1 ? 'text-success' : 'text-muted'}`}>
                                    <FontAwesomeIcon type={requestMode == 1 ? 'solid' : ''} icon={'check-circle'} />
                                </span>
                            </div>
                            <div className={`cursor-pointer mb-4 d-flex border justify-content-between p-3 rounded-1 ${requestMode == 0 ? 'border-success' : ''}`} onClick={() => setRequestMode(0)}>
                                <div className="d-flex me-3 fw-normal">
                                    <FontAwesomeIcon icon={'message'} iconClass={'fs-2 mt-1'}/>
                                    <span className="text-start ps-2">
                                        <p className="h5 mb-0 fw-bold">{this.content?.mode_dm_title ?? ""}</p>
                                        <span className="fs-9">{this.content?.mode_dm_desc ?? ""}</span>
                                    </span>
                                </div>
                                <Form.Check.Input type={'radio'} name={'rights-via'} className="d-none" />
                                <span className={`w-20px fs-6 ${requestMode == 0 ? 'text-success' : 'text-muted'}`}>
                                    <FontAwesomeIcon type={requestMode == 0 ? 'solid' : ''} icon={'check-circle'} />
                                </span>
                            </div>
                        </div>
                    </div>

                    <div className="d-flex justify-content-end p-4 border-top">
                        <Button variant="secondary" className="me-2" onClick={this.props.hideUgc}>{window?.APP_CONTENT?.common?.cancel ?? ""}</Button>
                        <Button onClick={this.onNextStep}>{this.content?.btn_next ?? ""}</Button>
                    </div>
                </>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        // ugcData: state.moderation.ugcData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChooseMethod);
